#cc {
	// window
	background-color: $gunmetal;
	bottom: 20px;
	position: fixed;
	right: 20px;

	// small mode
	&.small {
		bottom: unset;
		top: 99%;
	}

	// clickable bar, top part of window
	.c {
		height: 20px;
	}

	// below the clickable bar, rest of window
	.p {
		padding: 0 20px 20px 20px;
	}

	a {
		color: white;
		float: none;
		font-size: 16px;
		font-weight: 600;
		line-height: 1.5;
		margin: 0;
		text-decoration: none;

		&:hover {
			text-decoration: underline;
		}
	}

	.b {
		background-color: $spanish-blue;
		color: white;
		font-size: 18px;

		&:hover {
			background-color: $ball-blue;
			text-decoration: none;
		}
	}

	p {
		color: $gray-500;
		font-size: 16px;
		line-height: 1.5;
		margin: 0 0 20px 0;
		max-width: 300px;
		text-align: left;
	}

	.r {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		width: 100%;

		& > * {
			-webkit-box-flex: 1;
			-ms-flex: 1;
			flex: 1;
			padding: 5px 10px;
			text-align: center;
		}
	}
}
