:root,
html {
	font-size: 20px;
}

// nav

.navbar-brand {
	font-weight: 500;
}

// contact

.grecaptcha-badge {
	display: none;
}

label[for=consent] {
	font-weight: 500;
}

// post content

blockquote {
	@include boxed-content;

	p {
		margin: 0;
	}
}

img {
	display: block;
	margin: 2rem auto;
	width: 75%;
}

p, pre, ol, ul, .highlighter-rouge {
	& + h1,
	& + h2,
	& + h3,
	& + h4,
	& + h5,
	& + h6 {
		margin-top: $paragraph-margin-bottom * 2;
	}
}

p {
	text-align: justify;
}

.content {
	p, li {
		a {
			font-weight: 500;
		}
	}
}

strong {
	color: $dark;
	font-weight: 500;
}

.table {
	@include inner-content;

	th {
		font-weight: 500;
	}
}

ul {
	margin-bottom: $paragraph-margin-bottom;

	p + & {
		margin-top: -$paragraph-margin-bottom / 2;
	}

	&.toc, &.toc & {
		font-weight: 500;
		list-style: none;
	}

	&.item-list li a {
		font-weight: 400;
	}

	&.item-list li:last-child {
		margin-bottom: 0 !important;
	}
}

@media print {
	div.highlight code {
		white-space: pre-wrap;
	}

	pre {
		border: none;
	}
}
