div.highlight {
	@include boxed-content;
	//background-color: $hl-bg;
	//border-left: 2px solid $hl-border;
	color: $hl-color;
	//margin: 2rem auto 2rem auto;
	//padding: .5rem .5rem .5rem 1.5rem;
	tab-size: 2;
	//width: 90%;

	code {
		white-space: pre;
	}

	pre {
		margin: 0px;
		padding: 0px;

		&.lineno {
			border-right: 1px dotted $hl-lineno-border;
			color: $hl-lineno;
			margin-right: 10px;
			padding-right: 5px;
		}
	}

	// Comments
	.c, .c1, .ch, .cm, .cp, .cpf, .cs {
		color: $hl-comments;
	}

	// Keywords
	.k, .kc, .kd, .kn, .kp, .kr, .kt {
		color: $hl-keywords;
		font-weight: bold;
	}

	// Names
	.n, .na, .nb, .nc, .no, .nd, .ni, .ne, .nf, .nl, .nn, .nx, .py, .nt, .nv, .bp, .fm, .vc, .vg, .vi, .vm {
		color: $hl-names;
	}

	// Numbers
	.il, .m, .mb, .mf, .mh, .mi, .mo {
		color: $hl-numbers;
	}

	// Strings
	.dl, .l, .ld, .s, .s1, .s2, .sa, .sb, .sc, .sd,.sh, .si, .sr, .ss, .sx, .w {
		color: $hl-strings;
	}
	.se {
		color: $hl-string-escapes;
	}

	// Symbols
	.o, .ow, .p {
		color: $hl-symbols;
	}

	// Misc.
	.err, .gd { color: theme-color("danger"); }
	.ge { font-style: italic; }
	.gi { color: theme-color("success"); }
	.gs { font-weight: bold; }
	.gu { color: $hl-comments; }
}