// COLORS
// --------------------------------------

$snow:          #F9F9F9;
$isabelline:    #ECECED;
$gainsboro:     #D9DADC;
$lavender-grey: #C6C7CA;
$ash-grey:      #B3B5B9;
$trolley-grey:  #7B7D84;
$davys-grey:    #555861;
$gunmetal:      #30343F;
$dark-gunmetal: #1F2229;
$eerie-black:   #1B1D23;
$ball-blue:     #22AED1;
$spanish-blue:  #016fb9;

$gray-100:   $snow;
$gray-200:   $isabelline;
$gray-300:   $gainsboro;
$gray-400:   $lavender-grey;
$gray-500:   $ash-grey;
$gray-600:   $trolley-grey;
$gray-700:   $davys-grey;
$gray-800:   $gunmetal;
$gray-900:   $dark-gunmetal;
$blue:       $spanish-blue;
$cyan:       $ball-blue;

$body-color:       $gray-700;
$code-color:       $cyan;
$kbd-color:        $gray-100;
$kbd-bg:           $gray-700;
$link-hover-color: $ball-blue;
$pre-color:        $gray-700;
$text-muted:       $gray-500;

//$hl-bg:             $gray-100;
//$hl-border:         $gray-300; //$blue;
$hl-color:          $pre-color;
$hl-comments:       $gray-500;
$hl-keywords:       $blue;
$hl-lineno:         $gray-600;
$hl-lineno-border:  $gray-400;
$hl-names:          $hl-color;
$hl-numbers:        $cyan;
$hl-strings:        $cyan;
$hl-string-escapes: $cyan;
$hl-symbols:        $blue;

$landing-h1:     $gray-800;
$landing-p:      $gray-600;
$landing-border: $gray-300;

// FONT
// --------------------------------------

$font-family-monospace:  Inconsolata, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace !default;
$font-family-sans-serif: 'Barlow Condensed', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol" !default;
$font-weight-base:       300;
$link-hover-decoration:  none;

// CUSTOM SIZING
// --------------------------------------

$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 740px,
  xl: 760px
);

$headings-margin-bottom: 2rem;
$nav-link-padding-y: .25rem;
$navbar-padding-y: .25rem;
$paragraph-margin-bottom: 2rem;

